.gallery {
  max-width: 1200px;
  box-sizing: border-box;
  flex-wrap: wrap;
  justify-content: space-between;
  margin: 0 auto;
  display: flex;
}

.photo-card {
  width: 23%;
  box-sizing: border-box;
  border: 1px solid #ccc;
  border-radius: 10px;
  margin-bottom: 20px;
  padding: 10px;
  transition: all .3s;
  overflow: hidden;
  box-shadow: 0 2px 5px rgba(0, 0, 0, .1);
}

.photo-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 4px 10px rgba(0, 0, 0, .2);
}

.photo-card img {
  max-width: 100%;
  height: auto;
  margin-bottom: 10px;
  display: block;
}

.info {
  flex-wrap: wrap;
  justify-content: space-between;
  display: flex;
}

.info-item {
  box-sizing: border-box;
  text-align: center;
  color: #666;
  text-transform: uppercase;
  flex: 1;
  margin-right: 10px;
  font-size: 14px;
}

.search-form {
  background-color: #f2f2f2;
  border: none;
  border-radius: 50px;
  justify-content: center;
  align-items: center;
  margin-bottom: 20px;
  display: flex;
  box-shadow: 0 5px 10px rgba(0, 0, 0, .2);
}

.input-style {
  color: #777;
  width: 300px;
  box-sizing: border-box;
  background-color: #f2f2f2;
  border: none;
  border-radius: 50px;
  padding: 10px 20px;
  font-size: 16px;
  transition: all .3s;
}

.input-style:focus {
  outline: none;
  box-shadow: 0 0 10px rgba(0, 0, 0, .2);
}

.search-btn {
  width: 40px;
  height: 40px;
  cursor: pointer;
  text-indent: -9999px;
  background-color: #1a2455;
  background-image: url("search.1a5dde6a.png");
  background-position: center;
  background-repeat: no-repeat;
  border: none;
  border-radius: 50%;
  margin-left: -20px;
  transition: all .3s;
}

.search-btn:hover {
  background-color: #2e3c7f;
}

.load-more {
  color: #fff;
  cursor: pointer;
  visibility: hidden;
  background-color: #1a2455;
  border: none;
  border-radius: 5px;
  justify-content: center;
  margin-top: 20px;
  padding: 10px 20px;
  font-size: 16px;
  font-weight: bold;
  transition: background-color .2s;
}

.load-more:hover {
  color: #1a2455;
  background-color: #fff;
}

